export const anamnesisVitaeCreator = (pregnancy, childbirth, birthWeight) => {
  return `дитина від ${pregnancy}-ої вагітності, пологи ${childbirth}-і, вага при народженні ${birthWeight} грам. У психомоторному розвитку від однолітків не відстає. Перенесені дитячі інфекційні захворювання (вітряна віспа, кір, скарлатина, кашлюк, епід.паротит, гепатит A,B,C,D), зі слів батьків, заперечують. Профілактичні щеплення, зі слів батьків, проведені не в повному обсязі. Алергологічний анамнез не обтяжений. Спадковий анамнез не обтяжений. Останні 21 день в контакті з інфекційними хворими не перебувала; розладів стільця у дитини та членів сім'ї не спостерігалося. `;
};
